
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.current {
  width: 50%;
  height: 5em;
  line-height: 2.5em;
  margin: 0 auto;
  transform: translateY(2em);
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  border-radius: 1em;
}



